/**
 * @jest-environment jsdom
 */
require('@testing-library/jest-dom');
const React = require('react');
const { render, screen } = require('@testing-library/react');

// ======================================================================
// TIDAK ADA MOCKING SAMA SEKALI
// ======================================================================


// ======================================================================
// BAGIAN 2: MENGAMBIL KODE MAHASISWA
// ======================================================================
if (!process.env.SUBMISSION_PATH) {
  throw new Error('SUBMISSION_PATH environment variable not set.');
}
// Kita require seluruh file apa adanya, membiarkan Node.js yang menangani import internalnya
const MyProfileV2 = require(process.env.SUBMISSION_PATH).default;


// ======================================================================
// BAGIAN 3: TES YANG FOKUS PADA HASIL AKHIR DI LAYAR
// ======================================================================
describe('Praktikum: Komponen MyProfileV2', () => {

  // Render komponen mahasiswa sebelum setiap tes
  beforeEach(() => {
    // Jika ada error di sini, berarti ada masalah sintaks di file mahasiswa
    render(<MyProfileV2 />);
  });

  test('Kriteria 1: Harus menampilkan dua judul, yaitu "Foto" dan "Tentang"', () => {
    // Kita cari elemen heading yang berisi teks "Foto" dan "Tentang"
    const photoHeading = screen.getByRole('heading', { name: /Foto/i });
    const aboutHeading = screen.getByRole('heading', { name: /Tentang/i });
    expect(photoHeading).toBeInTheDocument();
    expect(aboutHeading).toBeInTheDocument();
  });

  test('Kriteria 2: Harus menampilkan gambar avatar Aklilu Lemma', () => {
    const avatarImage = screen.getByAltText('Aklilu Lemma');
    expect(avatarImage).toBeInTheDocument();
  });

  test('Kriteria 3: Gambar harus memiliki class "avatar"', () => {
    const avatarImage = screen.getByAltText('Aklilu Lemma');
    expect(avatarImage).toHaveClass('avatar');
  });

  test('Kriteria 4: Harus menampilkan paragraf deskripsi tentang Aklilu Lemma', () => {
    const description = screen.getByText(/seorang ilmuwan terkemuka dari Etiopia/i);
    expect(description).toBeInTheDocument();
    expect(description.tagName.toLowerCase()).toBe('p');
  });

  test('Kriteria 5: Komponen "MyProfileV2" harus diexport dengan benar', () => {
      expect(MyProfileV2).toBeDefined();
  });

});
